r {\n    box-sizing: border-box;\n}\n\n* {\n    margin: 0;\n    padding: 0;\n}\n\nbody {\n    line-height: 1.5;\n    -webkit-font-smoothing: antialiased;\n}\n\nimg,\npicture,\nvideo,\ncanvas,\nsvg {\n    display: block;\n    /* max-width: 100%; */\n}\n\ninput,\nbutton,\ntextarea,\nselect {\n    font: inherit;\n}\n\np,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n    overflow-wrap: break-word;\n}\n\n#root,\n#__next {\n    isolation: isolate;\n}\n\n/* -------------------------------------------------------------------------\n*/\n/* @font-face {\n    font-family: myFirstFont;\n    src: url('./fonts/star-jedi-font/StarJedi-DGRW.ttf') format('truetype');\n} */\n\n\n:root {\n    font-size: 12px;\n\n    --font-size-small: 1rem;\n    --font-size-medium: 1.5rem;\n    --font-size-large: 1.7rem;\n    --font-size-jumbo: 4rem;\n\n    --page-width: 750px;\n\n\n    --big-icon-side-length: 100px;\n    --search-section-length: calc(1.5 * var(--big-icon-side-length));\n\n    --standard-gap: 10px;\n\n\n    --general-padding: 20px;\n    --text-box-padding: 5px;\n\n\n\n    --button-height: 40px;\n    --button-padding: 5px;\n\n\n    white-space: nowrap;\n    --general-border-radius: 15px;\n\n\n}\n\n\n\n\n.dark {\n    /* Nighttime Color Scheme*/\n    --primary-background-color: #083344;\n    --primary-font-color: #fef3c7;\n\n    --high-temp-color: red;\n    --low-temp-color: rgb(67, 189, 87);\n    --humidity-color: rgb(115, 198, 185);\n\n\n}\n\n.light {\n    /* Morning/Day/Evening Color Scheme*/\n    --primary-background-color: #fef3c7;\n    --primary-font-color: #1c1917;\n\n    --high-temp-color: red;\n    --low-temp-color: blue;\n    --humidity-color: rgb(132, 73, 215);\n}\n\nbody {\n    background-image: url(\"/home/asteroidblues30/repos/star-wars-weather/src/backgrounds/stars.jpg\");\n    background-size: cover;\n\n    display: flex;\n    justify-content: center;\n    align-items: center;\n\n    /* height: 100vh; */\n}\n\n\n\n\n.main-container {\n\n    /* font-family: myFirstFont; */\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n    align-items: center;\n    gap: 15px;\n\n    border-radius: var(--general-border-radius);\n    /* width: 85vw; */\n    min-width: 1250px;\n\n    margin: 50px;\n    border: 10px solid var(--primary-background-color);\n\n    background-color: var(--primary-background-color);\n    /* background-image: url(\"./backgrounds/sunset-bespin.png\"); */\n    height: 80vh;\n\n}\n\n\n\n\n\n.search-container {\n    display: flex;\n    width: var(--search-section-length);\n\n    justify-content: space-between;\n\n\n}\n\n.header {\n    /* height: 10vh; */\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    gap: var(--standard-gap);\n\n    width: 100%;\n\n    height: 100px;\n\n}\n\n\n.today-header {\n    font-size: var(--font-size-jumbo);\n    text-align: center;\n\n    /* padding: var(--general-padding); */\n\n    /* border: 2px solid black; */\n\n    background-color: var(--primary-background-color);\n    color: var(--primary-font-color);\n    white-space: nowrap;\n}\n\n.convert-temp-btn {\n    font-size: var(--font-size-large);\n    width: 180px;\n    height: var(--button-height);\n   \n    border-radius: var(--general-border-radius);\n    padding: var(--button-padding);\n}\n\n.search-bar {\n    font-size: var(--font-size-large);\n    /* align-self: center; */\n    width: 300px;\n    height: var(--button-height);\n    margin-left: auto;\n    border-radius: var(--general-border-radius);\n\n    /* padding: var(--text-box-padding); */\n\n    padding: var(--button-padding);\n}\n\n.search-btn {\n    font-size: var(--font-size-large);\n    border-radius: var(--general-border-radius);\n    width: 100px;\n    height: var(--button-height);\n    padding: var(--button-padding);\n}\n\n.middle-section {\n    width: 100%;\n    height: 70vh;\n    border-radius: var(--general-border-radius);\n    display: flex;\n    justify-content: center;\n    align-items: center;\n\n    background-position: 0 center;\n    background-repeat: no-repeat;\n    background-size: cover;\n\n    position: relative;\n}\n\n\n\n.todays-weather-container {\n\n    width: 500px;\n    /* display: grid; */\n\n\n    display: flex;\n    flex-direction: column;\n    gap: 15px;\n\n    border: 2px solid black;\n    padding: 20px;\n    border-radius: var(--general-border-radius);\n\n    background-color: var(--primary-background-color);\n    color: var(--primary-font-color);\n\n\n}\n\n.weather-container-header {\n    display: flex;\n    width: 100%;\n    gap: var(--standard-gap);\n}\n\n.todays-weather-location {\n    font-size: var(--font-size-large);\n    /* text-align: center; */\n    /* grid-column: 1 / 4; */\n\n    justify-content: space-between;\n    align-items: center;\n    background-color: var(--primary-background-color);\n    overflow: hidden;\n    text-overflow: ellipsis;\n\n}\n\n.todays-weather-localtime {\n\n    margin-left: auto;\n    font-size: var(--font-size-large);\n    white-space: nowrap;\n}\n\n.weather-container-second-row {\n    display: flex;\n    gap: 25px;\n    justify-content: left;\n}\n\n.todays-icon-container {\n    grid-row: 2 / 4;\n\n\n    width: var(--big-icon-side-length);\n    height: var(--big-icon-side-length);\n\n    /* border: 2px solid black; */\n    background-position: center center;\n    background-repeat: no-repeat;\n    background-size: contain;\n\n    flex: 0 0 auto;\n\n    background-color: white;\n\n}\n\n\n.temp-and-conditions-container {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n\n}\n\n.current-conditions {\n    font-size: var(--font-size-large);\n}\n\n.current-temp {\n    font-size: var(--font-size-jumbo);\n    /* margin-left: auto; */\n\n\n    /* border: 2px solid black; */\n    background-color: var(--primary-background-color);\n}\n\n\n.high-low-box {\n    display: flex;\n    flex-direction: column;\n\n    /* margin-right:auto; */\n    justify-content: center;\n}\n\n/* .low-box {\n    grid-column: 2 / 3;\n\n\n}\n\n.high-box {\n    grid-column: 3 / 4;\n\n\n} */\n\n\n\n.condition-row {\n    font-size: var(--font-size-large);\n    /* border: 2px solid black; */\n\n    display: flex;\n    /* align-self: start;\n    justify-self: center; */\n\n    background-color: var(--primary-background-color);\n    gap: 5px;\n}\n\n.high {\n    color: var(--high-temp-color);\n    /* border: 2px solid black; */\n}\n\n.low {\n    color: var(--low-temp-color);\n    /* border: 2px solid black; */\n}\n\n.humidity {\n    color: var(--humidity-color);\n}\n\n\n\n.current-description {\n    font-size: var(--font-size-large);\n    /* text-align: center; */\n    /* border: 2px solid black; */\n\n    align-self: center;\n    justify-self: center;\n    grid-column: 1 / 4;\n\n    background-color: var(--primary-background-color);\n\n    /* white-space: ; */\n    white-space: wrap;\n}\n\n\n\n\n.upcoming-container {\n    display: flex;\n    border: 2px solid black;\n\n    /* height: 20vh; */\n    background-color: var(--primary-background-color);\n}\n\n.upcoming-day {\n    height: 100px;\n    width: 50px;\n}\n\n.weather-like{\n    position: absolute;\n    /* width: 150px; */\n    \n    border-radius: var(--general-border-radius);\n    padding: var(--button-padding);\n    font-size: var(--font-size-medium);\n    top: 90%;\n    left: 70%;\n    border: 3px solid black;\n    \n    background-color:  var(--primary-background-color);\n    color: var(--primary-font-color);\n}\n\n@media (max-width: 425px) {\n    :root {\n        font-size: 7px;\n\n        --font-size-large: 2rem;\n        --font-size-jumbo: 2.5rem;\n\n        --big-icon-side-length: 50px;\n\n        --button-height: 30px;\n        --button-padding: 4px;\n    }\n\n    body {\n        max-width: 350px;\n        overflow-y: scroll;\n        height: 100vh;\n        margin: 10px;\n        justify-content: center;\n        align-items: center;\n        \n\n    }\n\n    .main-container {\n        /* max-width: 400px; */\n        min-width: 0px;\n        height: 85vh;\n        margin: 10px;\n        gap: 5px;\n        justify-content: center;\n    }\n\n    .header {\n        /* height: 10vh; */\n        justify-content: center;\n        align-items: center;\n        flex-direction: column;\n        height: 150px;\n    }\n\n    .convert-temp-btn {\n\n       \n        width: 100px;\n\n    }\n\n\n    .search-bar {\n        margin-left: inherit;\n        width: 200px;\n     \n\n\n    }\n\n    .search-btn {\n        font-size: var(--font-size-large);\n        border-radius: var(--general-border-radius);\n        width: 100px;\n      \n    }\n\n    .middle-section {\n        width: 100%;\n        height: 500px;\n\n    }\n\n\n    .todays-weather-container {\n\n        width: 70%;\n        justify-content: center;\n        align-items: center;\n\n    }\n\n    .weather-container-header {\n        display: flex;\n        width: 100%;\n        gap: var(--standard-gap);\n        flex-direction: column;\n        align-items: center;\n        justify-content: center;\n    }\n\n    .todays-weather-location {\n    \n        text-align: center;\n        overflow: hidden;\n        text-overflow: ellipsis;\n        width: 90%;\n\n    }\n\n    .todays-weather-localtime {\n\n        margin-left: inherit;\n        font-size: var(--font-size-large);\n        white-space: nowrap;\n    }\n\n    .weather-container-second-row {\n    \n        flex-direction: column;\n      \n        justify-content: left;\n        align-items: center;\n    }\n\n    \n    \n\n    .condition-row {\n       \n        justify-content: space-around;\n    \n\n\n      \n    }\n\n\n    .weather-like{\n       \n        /* height: 40px; */\n       \n        padding: 3px;\n        font-size: var(--font-size-medium);\n        top: 90%;\n        left: 26%;\n        \n    }\n    \n\n}"],"sourceRoot":""}]);
